@import url(https://unpkg.com/papercss@1.8.2/dist/paper.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0 auto;
  padding: 0 auto;
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.input-login {
  left: 50%;
  position: absolute;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin-top: auto;
}

.name-input {
  margin-left: auto;
  margin-right: auto;
}

.inputs {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 10px;
  margin-top: 10px;
}


.buttons-login {
  margin: 6px;
}

.score {
  color: green;
}

.logo-header {
  -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
          animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
  border: none;
  width: 100px;
}

.header-questions {
  display: flex;
  justify-content: space-between;
}

.gravatal-img {
  align-items: center;
  display: flex;
  font-size: medium;
  justify-content: space-between;
  margin-bottom: 30px;
  min-width: 30%;
}

.red-border {
  border: 1px solid red;
}

.green-border {
  border: 1px solid green;
}

@media ( prefers-reduced-motion : no-preference ) {

  .App-logo {
    -webkit-animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
            animation: shake infinite 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    border: none;
  }
}

.App-header {
  align-items: center;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  justify-content: center;
  min-height: 100vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

@keyframes shake {

  10%, 90% {
    -webkit-transform: translate3d(-1px, 0, 0);
            transform: translate3d(-1px, 0, 0);
  }

  20%, 80% {
    -webkit-transform: translate3d(2px, 0, 0);
            transform: translate3d(2px, 0, 0);
  }

  30%, 50%, 70% {
    -webkit-transform: translate3d(-4px, 0, 0);
            transform: translate3d(-4px, 0, 0);
  }

  40%, 60% {
    -webkit-transform: translate3d(4px, 0, 0);
            transform: translate3d(4px, 0, 0);
  }
}

.correct:disabled {
  border: 3px solid rgb(6, 240, 15);
}

.wrong:disabled {
  border: 3px solid red;
}


.gravatarImg {
  margin-left: auto;
  margin-right: auto;
}
